.button {
  display: flex;
  justify-content: center;
  align-items: center;

  box-sizing: border-box;
  height: 56px;
  width: 100%;
  border: 2px solid $colorBlue;
  border-radius: 8px;
  cursor: pointer;

  text-decoration: none;
  color: $colorBlue;
  font-weight: bold;
  font-size: 16px;
  line-height: 14px;

  transition: all 0.3s;

  &--primary {
    background-color: #0075FF;
    border-color: #0075FF;
    box-shadow: 0px 6px 23px rgba(0, 117, 255, 0.46);
    color: #fff;
  }

  &:hover {
    background-color: #005ECC;
    border-color: #005ECC;
    box-shadow: 0px 13px 27px rgba(0, 83, 181, 0.46);
    color: #fff;
  }

  &:active {
    background-color: #005ECC;
    border-color: #005ECC;
    box-shadow: 0px 8px 14px rgba(0, 83, 181, 0.48);
    columns: #fff;
  }
}
