.contact-us {
  &__info {
    margin: 0 0 24px;

    &--last {
      margin: 0 0 32px;
    }

    &-title {
      margin: 0 0 8px;
      font-size: 14px;
      line-height: 27px;
      color: #9FADC2;
    }

    &-link {
      text-decoration: none;
      color: inherit;

      &:hover {
        color: $colorBlue;
      }
    }
  }

  &__field {
    margin: 0 0 16px;

    &--last {
      margin-bottom: 32px;
    }
  }

}